// @flow
import React from 'react';
import styled from 'styled-components';

import { COLORS, BREAKPOINTS } from '../../constants';

import Spacer from '../Spacer';
import Heading from '../Heading';
import Paragraph from '../Paragraph';
import Link from '../Link';
import MaxWidthWrapper from '../MaxWidthWrapper';

const IntroRouteEnd = () => {
  return (
    <Wrapper>
      <MaxWidthWrapper>
        <Spacer size={128} />
        <CongratsHeading>
          ¡Llegamos al final!
        </CongratsHeading>
        <LimitedParagraph>
          Esta es una adaptación del sitio web{' '}
          <Link external to="https://pudding.cool/2018/02/waveforms/">
            Let's Learn About Waveforms
          </Link>{' '}
          de Josh Comeau.
        </LimitedParagraph>

        <LimitedParagraph>
          Muchas gracias por interactuar con esta guía. ¡Esperamos que
          haya sido inspirador y educativo!
        </LimitedParagraph>
        
        <LimitedParagraph>
          Para contactar a Josh, podemos hacerlo{' '}
          <Link external to="https://twitter.com/JoshWComeau">
            por Twitter
          </Link>,{' '}
          o bien{' '}
          <Link external to="mailto:joshwcomeau@gmail.com">
            por email
          </Link>. Se aceptan sugerencias sobre temas relacionados con
          audio digital que quisiéramos ver explicados de esta forma.
        </LimitedParagraph>

        <LimitedParagraph>
          Este proyecto fue creado con React.{' '}
          <Link external to="https://github.com/joshwcomeau/waveforms">
            Ver el código
          </Link>.
        </LimitedParagraph>

        <LimitedParagraph>
          Este proyecto está fueremente inspirado en la fabulosa{' '}
          <Link
            external
            to="http://www.r2d3.us/visual-intro-to-machine-learning-part-1/"
          >
            intro visual a machine learning
          </Link> de R2D3.
        </LimitedParagraph>

        <LimitedParagraph />

        <LimitedParagraph>
          Josh quisiera agradece a Pat McNeil, su instructor en el{' '}
          <Link external to="http://www.vaniercollege.qc.ca/audio/">
            Vanier College
          </Link>, quien le enseñó todas estas cosas originalmente, y a 
          Matt Dunn-Rankin, compañero de trabajo en la{' '}
          <Link external to="https://www.khanacademy.org/">
            Khan Academy
          </Link>. La profunda experiencia de Pat acerca del audio y el 
          buen ojo de Matt por la pedagogía mejoraron mucho esta guía.
        </LimitedParagraph>

        <Spacer size={172} />
      </MaxWidthWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  margin-top: 64px;
  background: ${COLORS.gray[300]};
`;

const CongratsHeading = styled(Heading)`
  font-size: 60px;
  font-weight: 700;
  letter-spacing: -2px;
  text-align: center;
  margin-bottom: 60px;

  @media ${BREAKPOINTS.sm} {
    font-size: 48px;
  }

  @media ${BREAKPOINTS.mdMin} {
    font-size: 72px;
  }
`;

const LetterSquash = styled.span`
  letter-spacing: -7px;
`;

const LimitedParagraph = styled(Paragraph)`
  max-width: 600px;
  font-size: 26px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 42px;
`;

export default IntroRouteEnd;



